.navbar{
    height: 100px;
    background-color: rgb(241, 99, 99);
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between; 
}

.brand{
    height: 100%;
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    color: aliceblue;
}

.links{
    height: 100%;
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
}

.categoria{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: aliceblue;
    height: 100%;
    width: 25%;
}

.categoria:hover{
    background-color: pink;
    cursor: pointer;
}